import React, { Component } from 'react';
import Navigation from '../components/Navigation';
import queryString from 'query-string'
import News from '../components/News'

class ArticlePage extends Component{
    componentDidMount() {
        console.log(this.props.location.search)
        const values = queryString.parse(this.props.location.search)
        //const id = values.id
        
    }
    render(){
        let id = queryString.parse(this.props.location.search).id
        let newsWeb = localStorage.getItem("newsWeb");
        if (localStorage.getItem("currentWeb") !== null) {
            if (localStorage.getItem('newsWeb') !== localStorage.getItem('currentWeb')){
                newsWeb = localStorage.getItem("currentWeb")
                localStorage.setItem("currentWeb", localStorage.getItem("newsWeb"))
            }

        }
        
        
        return(
            <>
            <Navigation activeKey = {null} display = {'none'} page = 'article'/>
            <News page = {'article'} newsWeb = {newsWeb} id = {id} />
           {/* <News section = {section} newsWeb = {newsWeb}/>  */}
            </>
        )

    }
   

}

export default ArticlePage;