import React, { Component } from 'react';
import Navigation from '../components/Navigation';
import FavoriteCard from '../components/FavoriteCard';

class FavoritePage extends Component{
    componentDidMount() {
        document.getElementById('fareg').style.display = 'none';
        document.getElementById('fa').style.display = 'block'
        
    }
    render(){
       

        var favoritesList = JSON.parse(localStorage.getItem("favorites"))
        var favoritesKeys = Object.keys(favoritesList)
        if (favoritesKeys.length === 0 ) {
            return(
                <>
                <Navigation activeKey = {null} display = {'none'} page = {'favorite'}/>
                <div>no favorite</div>
                </>
            )
        }else {
            
            console.log(favoritesKeys)
            return (
                <>
                <Navigation activeKey = {null} display = {'none'} page = {'favorite'}/>
                <main>

            
            <div className = "row">
                {
                favoritesKeys.map(key => {
                
                let title = favoritesList[key].titleLove;
                let img = favoritesList[key].imgLove;
                let date = favoritesList[key].dateLove;
                let section = favoritesList[key].sectionLove
                let newsWeb = favoritesList[key].newsWebLove
                return(
                    <>
                    <FavoriteCard title = {title} img = {img} date = {date} section = {section} newsWeb = {newsWeb} id = {key}/>
                    </>
    
                )
            })
        }</div>
        </main></>
        )

        }
        
        
        

        return(
            <>
            
            
            {/* <News page = {'result'} newsWeb = {localStorage.getItem('newsWeb')} q = {q} /> */}
            </>
        )

    }
   

}

export default FavoritePage;